.themes-header {
  color: #fff;
  background: linear-gradient(135deg, #4f46e5 0%, #6366f1 100%);
  padding: 4rem 0;
}

.themes-header h1 {
  margin-bottom: 1rem;
  font-weight: 700;
}

.themes-header .lead {
  color: #ffffffe6;
  font-size: 1.125rem;
}

.theme-card {
  background: #fff;
  border-radius: 1rem;
  flex-direction: column;
  height: 100%;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  overflow: hidden;
  box-shadow: 0 1px 3px #0000001a, 0 1px 2px #0000000f;
}

.theme-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 20px 25px -5px #0000001a, 0 10px 10px -5px #0000000a;
}

.theme-preview {
  background: #f8fafc;
  border-bottom: 1px solid #e5e7eb;
  padding: 1.25rem;
  transition: background-color .2s;
}

.theme-card:hover .theme-preview {
  background: #f1f5f9;
}

.preview-banner {
  color: #fff;
  background: #4f46e5;
  border-radius: .375rem .375rem 0 0;
  align-items: center;
  gap: .5rem;
  padding: .5rem .75rem;
  font-size: .875rem;
  display: flex;
}

.preview-banner i {
  font-size: 1rem;
}

.preview-content {
  background: #fff;
  border: 1px solid #e5e7eb;
  border-top: none;
  border-radius: 0 0 .375rem .375rem;
  flex-direction: column;
  gap: .75rem;
  height: calc(100% - 2.5rem);
  padding: 1rem;
  display: flex;
  position: relative;
}

.preview-content:before {
  content: "";
  background: #f3f4f6;
  border-radius: .25rem;
  width: 60%;
  height: 1.25rem;
  display: block;
}

.preview-content:after {
  content: "";
  opacity: .7;
  background: repeating-linear-gradient(0deg, #f3f4f6 0 8px, #0000 8px 16px);
  border-radius: .25rem;
  flex-grow: 1;
  display: block;
}

.classic-driver-preview .preview-content:before {
  background: #e0e7ff;
  width: 70%;
}

.modern-driver-preview .preview-content:before {
  background: linear-gradient(90deg, #e0e7ff 0%, #c7d2fe 100%);
  width: 65%;
}

.classic-degree-preview .preview-content:before {
  background: #cffafe;
  width: 75%;
}

.modern-degree-preview .preview-content:before {
  background: linear-gradient(90deg, #cffafe 0%, #a5f3fc 100%);
  width: 80%;
}

.generic-theme-preview .preview-content:before {
  background: linear-gradient(90deg, #dbeafe 0%, #bfdbfe 100%);
  width: 50%;
}

.classic-driver-preview .preview-content:after, .modern-driver-preview .preview-content:after, .classic-degree-preview .preview-content:after, .modern-degree-preview .preview-content:after, .generic-theme-preview .preview-content:after {
  opacity: .5;
  background-size: 100% 16px;
}

.theme-info {
  flex-direction: column;
  flex-grow: 1;
  padding: 1.5rem;
  display: flex;
}

.theme-info h3 {
  color: #111827;
  margin-bottom: .75rem;
  font-size: 1.25rem;
  font-weight: 600;
  line-height: 1.4;
}

.theme-info p {
  color: #4b5563;
  flex-grow: 1;
  margin-bottom: 1.25rem;
  font-size: .9375rem;
  line-height: 1.5;
}

.theme-meta {
  flex-wrap: wrap;
  gap: .625rem;
  display: flex;
}

.theme-meta .badge {
  text-transform: uppercase;
  letter-spacing: .025em;
  border-radius: .375rem;
  padding: .4em .8em;
  font-size: .75rem;
  font-weight: 500;
}

.theme-development {
  background: #f8fafc;
}

.code-preview {
  background: #fff;
  border-radius: .75rem;
  overflow: hidden;
  box-shadow: 0 1px 3px #0000001a;
}

.code-header {
  color: #6b7280;
  background: #f9fafb;
  border-bottom: 1px solid #e5e7eb;
  padding: .75rem 1rem;
  font-family: Fira Code, monospace;
  font-size: .875rem;
}

.code-preview pre {
  margin: 0;
  padding: 1rem;
}

.code-preview code {
  font-family: Fira Code, monospace !important;
  font-size: .875rem !important;
}

.classic-driver-preview .preview-banner {
  background: #2563eb;
}

.modern-driver-preview .preview-banner {
  background: linear-gradient(135deg, #4f46e5 0%, #6366f1 100%);
}

.classic-degree-preview .preview-banner {
  background: #0891b2;
}

.modern-degree-preview .preview-banner {
  background: linear-gradient(135deg, #0891b2 0%, #0ea5e9 100%);
}

.generic-theme-preview .preview-banner {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
}

@media (width <= 768px) {
  .themes-header {
    padding: 3rem 0;
  }

  .theme-development {
    text-align: center;
  }

  .theme-development .d-flex {
    justify-content: center;
  }

  .code-preview {
    margin-top: 2rem;
  }
}
